



























































































































































import { mapState } from 'vuex'
import Vue from 'vue'
export default Vue.extend({
  components: {},
  props: {
    card: {
      type: Object,
      required: true,
    },
    titleClass: {
      type: String,
      required: false,
    },
    dialogClass: {
      type: String,
      required: false,
      default: 'upper-part',
    },
    showTitle: {
      type: Boolean,
      required: false,
      default: true,
    },
    cardClass: {
      type: String,
      required: false,
    },
    maxWidth: {
      type: Number,
      required: false,
      default: 0,
    },
    cols: {
      type: Number,
      required: false,
      default: 12,
    },
    colsSm: {
      type: Number,
      required: false,
    },
    colsMd: {
      type: Number,
      required: false,
    },
    payload: {
      type: Object,
      required: false,
    },
    eventName: {
      type: String,
      required: true,
    },
    buttonClassName: {
      type: String,
      required: false,
    },
    buttonText: {
      type: String,
      required: false,
    },
    isSelected: {
      type: Boolean,
      required: false,
      default: false,
    },
    hasButton: {
      type: Boolean,
      required: false,
      default: false,
    },
    iconColor: {
      type: String,
      required: false,
    },
    iconClass: {
      type: String,
      required: false,
    },
    selectedCurrency: {
      type: Object,
      required: true,
    },
  },

  methods: {
    onPricingCardToggled(event) {
      this.$emit('pricing-card-toggled', this.payload)
    },
  },
  watch: {},
})
